import React from "react"
import { NotificationManager as notifications } from "react-notifications"

const KEY_SETTING = "swagger-setting"

class SettingComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      urlProject: "",
      fullName: {
        value: ""
      },
      email:  {
        value: ""
      },
      accessToken:  {
        value: "pY79U5kZtkVuuLSORiSEPyZ07SS0aP2id4VFE49ZetMAP47r2L4UxIqjA06AiE7n"
      },
    }
  }

  componentWillMount = () => {
    const {
      settings,
      configs
    } = this.props

    const dataState = []

    if (settings.fullName) {
      dataState.fullName = {
        value: settings.fullName
      }
    }

    if (settings.email) {
      dataState.email = {
        value: settings.email
      }
    }

    if (settings.accessToken) {
      dataState.accessToken = {
        value: settings.accessToken
      }
    }

    if (configs.currentProject) {
      const urlProject = `${window.origin}#workspace=${configs.currentProject}`
      
      dataState.urlProject = urlProject
    }

    this.setState(dataState)
  }

  handleInputChange = (event) => {

    const stringValue = event.target.value
    const name = event.target.name

    const data = []

    data[name] = {
      value: stringValue
    }

    this.setState(data)
  }

  handleCopyUrl = () => {
    const input = document.getElementById("urlProject")
    input.select()
    const result = document.execCommand("copy")
    if (result) {
      notifications.success("Copy")
    }
}

  // save content to localstorage Workspace
  updateSetting = () => {
    const {
      updateLayout
    } = this.props

    const {
      fullName,
      email,
      accessToken,
    } = this.state

    const settingContent = {
      fullName: fullName.value,
      email: email.value,
      accessToken: accessToken.value
    }

    window.localStorage.setItem(KEY_SETTING, JSON.stringify(settingContent))

    updateLayout()

    notifications.success("Update user successfully", "Update Setting User")
  }

  render() {  
    const {
      fullName,
      email,
      accessToken,
      urlProject,
    } = this.state

    return (
      <div className="right-panel">
        <div className="card">
          <div className="card-body">
            <label>Url project</label>
            <div className="input-group">
                <input
                  type="text"
                  id="urlProject"
                  className="form-control"
                  value={urlProject}
                  readOnly
                />
                <div className="input-group-append">
                  <button type="button" className="btn btn-outline-secondary" onClick={this.handleCopyUrl} >Copy</button>
                </div>
              </div>
          </div>
        </div>
        <div className="card">
          <div className="card-body">
            <div className="form-group">
              <label>Full Name</label>
              <input
                type="text"
                className="form-control"
                placeholder="Enter name or username"
                name="fullName"
                id="fullName"
                value={fullName.value}
                onChange={this.handleInputChange}
                autoFocus
              />
            </div>
            <div className="form-group">
              <label>Email</label>
              <input
                type="email"
                className="form-control"
                placeholder="Enter Email"
                name="email"
                id="email"
                value={email.value}
                onChange={this.handleInputChange}
              />
            </div>
            <div className="form-group">
              <label>Access Token</label>
              <input
                type="text"
                className="form-control"
                placeholder="Enter Access Token"
                name="accessToken"
                id="accessToken"
                value={accessToken.value}
                onChange={this.handleInputChange}
              />
            </div>
          </div>
          <div className="card-footer">
            <button className="btn btn-info" style={{float: "right"}} onClick={this.updateSetting}>Update</button>
          </div>
        </div>
    </div>
    )
  }
}

export default SettingComponent
