import React from "react"
import axios from "axios"
import { NotificationManager as notifications } from "react-notifications"

const KEY_ITEM_FILE = "swagger-item-"
const CONFIG_KEY = "swagger-config"
const HISTORY_LOG = "history-log"


class PushComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      timeUpdate: ""
    }
  }

  // push all commit to server
  pushToServer = () => {
    const {
      configs,
      settings,
      fetchFromServer,
      updateLayout,
      histories,
    } = this.props

    let listCommit = []

    if (configs && configs.listCommit && configs.listCommit.length > 0) {
      listCommit = configs.listCommit

      const keyName = configs.currentProject ? configs.currentProject : ""
      
      const contentProject = window.localStorage.getItem(keyName)

      if (contentProject !== undefined) {
        const urlPost = "/api/project/push"

        let nameProject = ""

        if (keyName.includes(KEY_ITEM_FILE) === true) {
          nameProject = keyName.replace(KEY_ITEM_FILE, "")
        }

        const dataSend = {
          contents: contentProject,
          key: keyName,
          name: nameProject,
          countHistory: histories[keyName],
          lstCommit: listCommit,
          accessToken: settings.accessToken,
        }

        const header = {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*" ,
          "X-CSRF-TOKEN": window.$("meta[name='csrf-token']").attr("content")
        }
    
        axios.post(urlPost, dataSend, header)
        .then((response) => {
          if (response.status === 200) {
            fetchFromServer()

            updateLayout()

            const number = Number(histories[keyName]) + Number(configs.listCommit.length)

            if (number > histories[keyName]) {
              histories[keyName] = number
  
              window.localStorage.setItem(HISTORY_LOG, JSON.stringify(histories))
            }

            configs.listCommit = []

            window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))
            
            this.setState({
              timeUpdate: new Date().getTime()
            })

            notifications.success("Push file yml to server success!", "Push File Yml")
          }
        })
        .catch((err) => {
          console.error(err)
          notifications.error("Can not push file to server", "Push File Yml")
        })
      } 
    }
  }

  removeAllCommit = () => {
    const {
      configs,
      updateLayout
    } = this.props

    configs.listCommit = []

    window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))

    updateLayout()

    notifications.success("All commit being removed!", "Remove All Commit")

    this.setState({
      timeUpdate: new Date().getTime()
    })
  }

  renderListCommit = () => {
    const {
      configs
    } = this.props

    let commitLayout = ""

    if (configs && configs.listCommit && configs.listCommit.length > 0) {
      commitLayout = configs.listCommit.map((item, index) => {
        return (
        <div className="card" data-key={index.toString()}>
          <div className="card-body">
            <ul>
              <li>
                <b>Project:</b> {item.name}
              </li>
              <li>
                <b>file name:</b> {`${item.key}.yml`}
              </li>
              <li>
                <b>Author:</b> {item.user}
              </li>
              <li>
                <b>Date:</b> {item.date}
              </li>
              <li>
                <b>Comment:</b> {item.comment}
              </li>
            </ul>
          </div>
        </div>
        )
      })
    }

    return commitLayout
  }

  render() {
    const {
      configs
    } = this.props

    const {
      timeUpdate
    } = this.state

    const disabledButton = configs && configs.listCommit && configs.listCommit.length === 0

    return (
      <div className="right-panel" data-timeUpdate={timeUpdate}>
        <div className="history-panel">
          {this.renderListCommit()}
        </div>
        <div className="form-group">
        <button
            className="btn btn-danger"
            style={{
              float: "right",
              marginTop: "15px",
              marginRight: "15px",
            }}
            disabled={disabledButton}
            onClick={this.removeAllCommit}
          >
            Remove all commit
          </button>
          <button
            className="btn btn-success"
            style={{
              float: "right",
              marginTop: "15px",
              marginRight: "15px",
            }}
            disabled={disabledButton}
            onClick={this.pushToServer}
          >
            Push
          </button>
        </div>
    </div>
    )
  }
}

export default PushComponent
