import React from "react"
import { NotificationManager as notifications } from "react-notifications"

const KEY_ITEM_FILE = "swagger-item-"
const CONFIG_KEY = "swagger-config"
const KEY_WORK_CONTENT = "swagger-editor-content"

class RenameComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      nameProject: {
        value: ""
      },
      currentName: "",
      valid: ""
    }
  }

  componentWillMount = () => {
    //
  }

  handleInputChange = (event) => {
    const stringValue = event.target.value
    const name = event.target.name

    const data = []

    const reWhiteSpace = new RegExp(/^\s+$/)

    if (reWhiteSpace.test(stringValue) === false) {
      data[name] = {
        value: stringValue.replace(" ", "")
      }

      data.valid = ""
    } else {
      data.valid = "Can not enter space in name project!"
    }

    this.setState(data)
  }

  handleCheckboxChange = (event) => {

    const stringValue = event.target.checked
    const name = event.target.name

    const data = []

    data[name] = {
      value: stringValue
    }

    this.setState(data)
  }

  handleRenameProject = () => {
    const {
      updateLayout,
      data,
      configs,
      setTitleHeader,
    } = this.props

    const {
      nameProject,
    } = this.state

    const contentWorkspace = window.localStorage.getItem(data.key)

    if (window.localStorage && nameProject.value) {
      const keyForName = nameProject.value.toLowerCase().trim()

      const newNameProject = `${KEY_ITEM_FILE}${keyForName}`

      window.localStorage.setItem(newNameProject, contentWorkspace)

      window.localStorage.removeItem(data.key)

      if (configs.currentProject && configs.currentProject === data.key) {
        configs.currentProject = newNameProject

        window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))

        setTitleHeader(keyForName)
      }
      
      this.setState({
        nameProject: {
          value: ""
        }
      })

      window.location.hash = ""

      updateLayout()

      notifications.success(`Rename ${data.name} to ${nameProject.value} successfully`, "Rename Project")
    } else {
      notifications.error(`Please enter name project`, "Rename Project")
    }
  }

  render() {  
    const {
      data
    } = this.props

    const {
      nameProject,
      valid,
    } = this.state

    let validMessage = ""

    if (valid.length > 0) {
      validMessage = (
        <span style={{color: "red"}}>{valid}</span>
      )
    }

    return (
      <div className="right-panel">
        <div className="card">
          <div className="card-body">
            <div className>
              <p>Current Project: {data.name || ""}</p>
            </div>
            <div className="form-group">
              <label>New Name Project</label>
              <input
                type="text"
                className="form-control"
                name="nameProject"
                id="nameProject"
                value={nameProject.value}
                onChange={this.handleInputChange}
                pattern="[^\s]+"
                autoFocus
              />
              {validMessage}
              <span>{nameProject.value ? `file name: ${KEY_ITEM_FILE}${nameProject.value}.yml` : ""}</span>
            </div>
          </div>
          <div className="card-footer">
            <button
              className="btn btn-info"
              style={{float: "right"}}
              onClick={this.handleRenameProject}
            >
              Rename project
            </button>
          </div>
        </div>
    </div>
    )
  }
}

export default RenameComponent
