import React from "react"
import axios from "axios"
import { NotificationManager as notifications } from "react-notifications"

// import component
import ProjectComponent from "./project"
import RenameProjectComponent from "./rename"
import CommitComponent from "./commit"
import PushComponent from "./push"
import SettingComponent from "./setting"

const LAYOUT = {
  DASHBOARD: "DASHBOARD",
  PUSH: "PUSH",
  PULL: "PULL",
  FETCH: "FETCH",
  PROJECT: "PROJECT",
  RENAME: "RENAME",
  SETTING: "SETTING",
}

const KEY_ITEM_FILE = "swagger-item-"
const CONFIG_KEY = "swagger-config"
const HISTORY_LOG = "history-log"

class TeamComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      layout: LAYOUT.DASHBOARD,
      listProjectServer: [],
      historiesServer: [],
      changeNumber: 0,
      dataRename: {
        name: "",
        key: ""
      }
    }
  }

  componentDidMount = () => {
    //
    this.handleFetchFromServer()
    this.getProjectFromUrl()
  }

  // get sharing project
  getProjectFromUrl = () => {
    const urlBase = window.location.hash
    const {
      specActions,
      settings,
      configs,
      setTitleHeader,
    } = this.props
    
    if (urlBase) {
      const indexObj = urlBase.indexOf("#workspace=")

      if (indexObj > -1) {
        const keyName = urlBase.replace("#workspace=", "")
        const {
          accessToken
        } = settings
    
        const url = `/api/project/detail/${keyName}?accessToken=${accessToken}`
    
        axios.get(url)
        .then((response) => {
          // handle success
          if (response.status === 200 && response.data && response.data.content) {
            window.localStorage.setItem(keyName, response.data.content)
            specActions.updateSpec(response.data.content)

            configs.currentProject = keyName
      
            window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))

            setTitleHeader(keyName)
            this.setState({
              currentProject: keyName,
            })
          } else {
            notifications.warning("Can not get file *.yml from server", "Get Project Detail")
          }
        })
        .catch((err) => {
          // handle error
          notifications.error("Can not get file *.yml from server", "Get Project Detail")

          console.error(err)
        })
      }
    }
  }
  
  // get list project server
  getListProjectServer = () => {
    const { settings } = this.props
    const {
      accessToken
    } = settings

    const param = {
      accessToken: accessToken,
    }

    const paramString = new URLSearchParams(param)

    const url = `/api/project/list?${paramString.toString()}`
  
    axios.get(url)
    .then((response) => {
      // handle success
      if (response.status === 200 && response.data) {
        this.setState({
          listProjectServer: response.data,
        })
      }
    })
    .catch((err) => {
      // handle error
      notifications.error("Can not get list project from server", "Get Project List")

      console.error(err)
    })
  }

  // get list project server
  getHistoryFromServer = () => {
    const { settings } = this.props
    const {
      accessToken
    } = settings

    const url = `/api/project/history/list?accessToken=${accessToken}`
  
    axios.get(url)
    .then((response) => {
      // handle success
      if (response.status === 200 && response.data) {
        this.setState({
          historiesServer: response.data,
        })
      }
    })
    .catch((err) => {
      // handle error
      notifications.error("Can not get list histories from server", "Get History List")

      console.error(err)
      })
  }

  checkOutFileFromServer = (keyName, name) => {
    const {
      specActions,
      settings,
      setTitleHeader,
      configs,
      histories,
    } = this.props
    const {
      accessToken
    } = settings

    const param = {
      accessToken: accessToken,
    }

    const paramString = new URLSearchParams(param)

    const url = `/api/project/detail/${keyName}?${paramString.toString()}`
  
    const lstCommitEmpty = configs && configs.listCommit && configs.listCommit.length > 0 

    const numberRows = histories[keyName]

    if (lstCommitEmpty === false) {
      axios.get(url)
      .then((response) => {
        // handle success
        if (response.status === 200 && response.data) {
          const {
            content,
            countHistory,
          } = response.data

          if (numberRows === undefined || numberRows >= countHistory) {
            window.localStorage.setItem(keyName, content)
            specActions.updateSpec(content)
    
            configs.currentProject = keyName

            histories[keyName] = countHistory

            setTitleHeader(name)
    
            window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))
            window.localStorage.setItem(HISTORY_LOG, JSON.stringify(histories))
    
            this.setState({
              currentProject: keyName,
              changeNumber: 0
            })

            notifications.success(`Checkout ${keyName} successfully!`, "Checkout File")
          } else {
            const numRows = histories[keyName] || 0
            if (configs.currentProject === keyName) {
              this.setState({
                changeNumber: countHistory - numRows
              })
            }
            notifications.error(`Has ${countHistory - numRows} commit be changed by someone!`, "Checkout File")
          }
        }
      })
      .catch((err) => {
        notifications.error("Can not get file *.yml from server", "Get Project Detail")

        console.error(err)
      })
    } else {
      notifications.warning("can not checkout other project. Please push all commit before change project")
    }
  }
  
  pullFileFromServer = () => {
    const {
      specActions,
      settings,
      setTitleHeader,
      configs,
      histories,
    } = this.props
    const {
      accessToken
    } = settings

    const param = {
      accessToken: accessToken,
    }

    const paramString = new URLSearchParams(param)

    const url = `/api/project/detail/${configs.currentProject}?${paramString.toString()}`
  
    const lstCommitEmpty = configs && configs.listCommit && configs.listCommit.length > 0 

    if (lstCommitEmpty === false) {
      axios.get(url)
      .then((response) => {
        // handle success
        if (response.status === 200 && response.data) {
          const {
            content,
            countHistory,
          } = response.data

          window.localStorage.setItem(configs.currentProject, content)
          specActions.updateSpec(content)
  
          histories[configs.currentProject] = countHistory

          setTitleHeader(name)
  
          window.localStorage.setItem(HISTORY_LOG, JSON.stringify(histories))

          notifications.success(`Pull ${name} successfully!`, "Pull File")

          this.setState({
            changeNumber: 0
          })
        }
      })
      .catch((err) => {
        notifications.error("Can not get file *.yml from server", "Get Project Detail")

        console.error(err)
      })
    } else {
      notifications.warning("can not checkout other project. Please push all commit before change project")
    }
  }

  handleFetchFromServer = () => {
    this.getListProjectServer()
    this.getHistoryFromServer()
  }

  openLayout = (layout) => {
    this.setState({
      layout: layout
    })
  }

  updateLayout = () => {
    const { updateLayoutParent } = this.props

    updateLayoutParent()

    this.setState({
      timeLastCreated: new Date().getTime()
    })
  }

  getListProject = () => {
    const listProject = {...window.localStorage}
    const lstWorkspace = []

    Object.keys(listProject).map((item) => {
      if (item.includes(KEY_ITEM_FILE) === true) {
        const workSpace = {
          name: item.replace(KEY_ITEM_FILE, ""),
          key: item
        }
        lstWorkspace.push(workSpace)
      }
    })

    return lstWorkspace
  }

  // share project

  copyAndShareProject = (key) => {
    const urlProject = `${window.origin}#workspace=${key}`

    var node = document.createElement("textarea")
    var selection = document.getSelection()
  
    node.textContent = urlProject
    document.body.appendChild(node)
  
    selection.removeAllRanges()
    node.select()
    document.execCommand("copy")
  
    selection.removeAllRanges()
    document.body.removeChild(node)

    notifications.success(`Copying to the clipboard!`)
  }

  // delete from server
  deleteProjectFromServer = (key, name) => {
    const {
      settings,
    } = this.props

    if (settings) {
      const urlPost = "/api/project/delete"

      const date = new Date()

      const author = `${settings.fullName} <${settings.email}>`
      const dateSummit = `${date.getUTCFullYear()}/${date.getUTCMonth()}/${date.getUTCDate()} ${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}`;
      

      const dataSend = {
        key: key,
        name: name,
        user: author,
        date: dateSummit,
        comment: `Delete project ${name}`,
        accessToken: settings.accessToken,
      }
      const header = {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*" ,
        "X-CSRF-TOKEN": window.$("meta[name='csrf-token']").attr("content")
      }
  
      axios.post(urlPost, dataSend, header)
      .then((response) => {
        if (response.status === 200 && response.data) {
          this.handleFetchFromServer()

          notifications.success(response.data.message, "Delete project")
        }
      })
      .catch((err) => {
        console.error(err)
        notifications.error("Can not delete project from serve!", "Delete project")
      })
    } 
  }

  // delete workspace
  deleteProject = (key, name) => {
    const {
      configs
    } = this.props

    if (configs && configs.currentProject && configs.currentProject === key) {
      notifications.warning(`Can not delete project: ${name}, please checkout other project`)
    } else {
      window.localStorage.removeItem(key)
      notifications.success(`Delete successful project: ${name}`)
    }

    this.updateLayout()
  }

  checkOutProject = (key, name) => {
    const {
      configs,
      setTitleHeader
    } = this.props

    const contentProject = window.localStorage.getItem(key)

    const lstCommitEmpty = configs && configs.listCommit && configs.listCommit.length > 0 

    if (configs && contentProject !== undefined && lstCommitEmpty === false) {
      this.props.specActions.updateSpec(contentProject)

      // save config
      configs.currentProject = key

      window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))

      window.location.hash = ""

      setTitleHeader(name)

      notifications.success(`Checkout project name: ${name}`)
    } else {
      notifications.warning("can not checkout other project. Please push all commit before change project")
    }

    this.updateLayout()
  }

  
  renameProject = (key, name) => {
    const dataRename = {
      name: name,
      key: key
    }

    this.setState({dataRename: dataRename})

    this.openLayout(LAYOUT.RENAME)
  }

  duplicateProject = (key, name) => {
    if (key && name) {
      const keyCopy = `${key}-copy`

      window.localStorage.setItem(keyCopy, "")

      const contentCurrent = window.localStorage.getItem(key)

      if (contentCurrent) {
        window.localStorage.setItem(keyCopy, contentCurrent)
      }

      this.updateLayout()

      notifications.success(`Duplicate project ${name} successfully!`)
    } else {
      notifications.warning(`Project not exist or empty!`)
    }
  }

  renderFileStatusLayout = () => {
    const {
      specActions,
      configs,
      settings,
      histories
    } = this.props

    const {
      historiesServer
    } = this.state

    return (
      <CommitComponent
        configs={configs}
        specActions={specActions}
        updateLayout={this.updateLayout}
        settings={settings}
        histories={histories}
        historiesServer={historiesServer}
        fetchFromServer={this.handleFetchFromServer}
      />
    )
  }

  renderPushLayout = () => {
    const {
      specActions,
      configs,
      settings,
      histories
    } = this.props

    return (
      <PushComponent
        configs={configs}
        specActions={specActions}
        updateLayout={this.updateLayout}
        settings={settings}
        histories={histories}
        fetchFromServer={this.handleFetchFromServer}
      />
    )
  }

  renderPullLayout = () => {
    return (
      <div className="right-panel">
        <div className="card">
          <div className="card-body">
            <h5>Are you sure pull new file *.yml from server?</h5>
            <span style={{ color: "red" }}>New content will replace current file at local!</span>
          </div>
          <div className="card-footer">
            <button className="btn btn-info" style={{float: "right"}} onClick={this.pullFileFromServer}>Pull</button>
          </div>
        </div>
    </div>
    )
  }

  renderProjectLayout = () => {
    const {
      specActions,
      configs,
      settings,
      histories,
      setTitleHeader,
    } = this.props

    return (
      <ProjectComponent
        configs={configs}
        specActions={specActions}
        updateLayout={this.updateLayout}
        settings={settings}
        histories={histories}
        setTitleHeader={setTitleHeader}
      />
    )
  }

  renderRenameProjectLayout = () => {
    const {
      specActions,
      configs,
      settings,
      histories,
      setTitleHeader,
    } = this.props

    const {
      dataRename
    } = this.state

    return (
      <RenameProjectComponent
        configs={configs}
        specActions={specActions}
        updateLayout={this.updateLayout}
        settings={settings}
        histories={histories}
        setTitleHeader={setTitleHeader}
        data={dataRename}
      />
    )
  }

  renderSettingLayout = () => {
    const {
      specActions,
      configs,
      settings,
      histories
    } = this.props

    return (
      <SettingComponent
        configs={configs}
        specActions={specActions}
        updateLayout={this.updateLayout}
        settings={settings}
        histories={histories}
      />
    )
  }

  renderListProjectWorkspace = () => {
    const {
      configs
    } = this.props

    const listWorkspaces = this.getListProject()

    let htmlLayout= ""
    let currentProject = ""

    if (configs && configs.currentProject) {
      currentProject = configs.currentProject
    }

    if (listWorkspaces && listWorkspaces.length > 0) {
      htmlLayout = listWorkspaces.map((item, index) => {
        return (
          <li key={index.toString()}>
            <a
              className={`btn btn-item ${currentProject === item.key ? "selected" : "" }`}
              type="button"
              id={item.key}
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              {item.name}
            </a>
            <div className="dropdown-menu" aria-labelledby={item.key}>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.checkOutProject(item.key, item.name)}
              >
                {`Checkout ${item.name}`}
              </a>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.renameProject(item.key, item.name)}
              >
                {`Rename ${item.name}`}
              </a>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.duplicateProject(item.key, item.name)}
              >
                {`Duplicate ${item.name}`}
              </a>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.deleteProject(item.key, item.name)}
              >
                {`Delete ${item.name}`}
              </a>
            </div>
          </li>
        )
      })
    }
    return htmlLayout
  }

  renderListProjectServer = () => {
    const {
      listProjectServer,
    } = this.state

    const {
      histories
    } = this.props

    let htmlLayout= ""

    if (listProjectServer && listProjectServer.length > 0) {
      htmlLayout = listProjectServer.map((item, index) => {
        const numRows = histories[item.key] || 0
        const numChanged = Number(item.history) - Number(numRows)

        const numDisplay = numChanged > 0 ? ` (${numChanged})` : ""

        return (
          <li key={index.toString()}>
            <a
              className={`btn btn-item`}
              type="button"
              id={item.key}
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              {`${item.name}${numDisplay}`}
            </a>
            <div className="dropdown-menu" aria-labelledby={item.key}>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.checkOutFileFromServer(item.key, item.name)}
              >
                {`Checkout server/${item.name}`}
              </a>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.deleteProjectFromServer(item.key, item.name)}
              >
                {`Delete server/${item.name}`}
              </a>
              <a
                className="btn-item dropdown-item"
                type="button"
                onClick={() => this.copyAndShareProject(item.key)}
              >
                {`Share server/${item.name}`}
              </a>
            </div>
          </li>
        )
      })
    }
    return htmlLayout
  }

  render() {
    const {
      openModal,
      closeModal,
      classNameModal,
      configs,
    } = this.props
    
    const {
      layout,
      timeLastCreated,
      changeNumber
    } = this.state

    let dashboardLayout = this.renderFileStatusLayout()

    switch (layout) {
      case LAYOUT.DASHBOARD:
        dashboardLayout = this.renderFileStatusLayout()
        break
      case LAYOUT.PUSH:
          dashboardLayout = this.renderPushLayout()
          break
      case LAYOUT.PULL:
          dashboardLayout = this.renderPullLayout()
          break
      case LAYOUT.FETCH:
          dashboardLayout = this.renderFetchLayout()
          break
      case LAYOUT.PROJECT:
          dashboardLayout = this.renderProjectLayout()
          break
      case LAYOUT.RENAME:
        dashboardLayout = this.renderRenameProjectLayout()
        break
      case LAYOUT.SETTING:
          dashboardLayout = this.renderSettingLayout()
          break
      default:
        dashboardLayout = this.renderFileStatusLayout()
        break
    }

    let numCommit = 0

    if (configs && configs.listCommit && configs.listCommit.length > 0) {
      numCommit = configs.listCommit.length
    }

    return (
      <div
        className={`workspace ${classNameModal || ""}`}
        style={{
          display: openModal === true ? "block" : "none"
        }}
      >
        <div className="container panel team-panel" data-timeUpdate={timeLastCreated}>
            <div className="tool-bar">
              <button type="button" className="btn btn-default btn-close" onClick={closeModal}>X</button>
            </div>
            <div className="header-panel">
              <button
                type="button"
                className={`btn btn-default ${layout === LAYOUT.DASHBOARD ? "active" : ""}`}
                onClick={() => this.openLayout(LAYOUT.DASHBOARD)
                }
              >
                Commit
              </button>
              <button
                type="button"
                className={`btn btn-default ${layout === LAYOUT.PULL ? "active" : ""}`}
                onClick={() => this.openLayout(LAYOUT.PULL)
                }
              >
                Pull
                {
                  changeNumber > 0 ? 
                  (<span className="num-commit">{changeNumber}</span>) : ""
                }
              </button>
              <button
                type="button"
                className={`btn btn-default ${layout === LAYOUT.PUSH ? "active" : ""}`}
                onClick={() => this.openLayout(LAYOUT.PUSH)
                }
              >
                Push
                {
                  numCommit > 0 ? 
                  (<span className="num-commit">{numCommit}</span>) : ""
                }
              </button>
              <button
                type="button"
                className={`btn btn-default ${layout === LAYOUT.FETCH ? "active" : ""}`}
                onClick={() => this.handleFetchFromServer()
                }
              >
                Fetch
              </button>
              <button
                type="button"
                className={`btn btn-default ${layout === LAYOUT.PROJECT ? "active" : ""}`}
                onClick={() => this.openLayout(LAYOUT.PROJECT)
                }
              >
                Project
              </button>
              <button
                type="button"
                className={`btn btn-default btn-right ${layout === LAYOUT.SETTING ? "active" : ""}`}
                onClick={() => this.openLayout(LAYOUT.SETTING)
                }
              >
                Setting
              </button>
            </div>
            <div className="body-panel">
              <div className="left-panel">
                <a type="button" className="btn btn-default title">WORKSPACE</a>
                <ul>
                  {this.renderListProjectWorkspace()}
                </ul>
                <a type="button" className="btn btn-default title">SERVER</a>
                <ul>
                  {this.renderListProjectServer()}
                </ul>
              </div>
              {dashboardLayout}
            </div>
          </div>
      </div>
    )
  }
}

export default TeamComponent
