import React from "react"
import axios from "axios"
import { NotificationManager as notifications } from "react-notifications"

const KEY_ITEM_FILE = "swagger-item-"
const CONFIG_KEY = "swagger-config"
const HISTORY_LOG = "history-log"
const KEY_WORK_CONTENT = "swagger-editor-content"

class CommitComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      comment: {
        value: ""
      },
      pushToServer:  {
        value: false
      },
      author: ""
    }
  }

  componentDidMount = () => {
    const {
      settings
    } = this.props

    const dataState = []

    if (settings.fullName && settings.email) {
      dataState.author = `${settings.fullName} <${settings.email}>`
    }

    this.setState(dataState)
  }

  handleInputChange = (event) => {

    const stringValue = event.target.value
    const name = event.target.name

    const data = []

    data[name] = {
      value: stringValue
    }

    this.setState(data)
  }

  handleCheckboxChange = (event) => {

    const stringValue = event.target.checked
    const name = event.target.name

    const data = []

    data[name] = {
      value: stringValue
    }

    this.setState(data)
  }

  pushProjectToServer = (data) => {
    const { fetchFromServer, histories } = this.props
    const contentProject = window.localStorage.getItem(data.key)

    if (contentProject !== undefined) {
      const urlPost = "/api/project/push"
      const lstCommit = [{
        user: data.user,
        date: data.date,
        comment: data.comment,
      }]

      const dataSend = {
        contents: contentProject,
        key: data.key,
        name: data.name,
        lstCommit: lstCommit,
        countHistory: histories[data.key],
        accessToken: data.accessToken,
      }
      const header = {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*" ,
        "X-CSRF-TOKEN": window.$("meta[name='csrf-token']").attr("content")
      }
  
      axios.post(urlPost, dataSend, header)
      .then((response) => {
        if (response.status === 200) {
          fetchFromServer()

          const number = Number(histories[data.key]) + 1

          if (number > histories[data.key]) {
            histories[data.key] = number

            window.localStorage.setItem(HISTORY_LOG, JSON.stringify(histories))
          }

          notifications.success("Commit && push file yml to server success!", "Commit")
        }
      })
      .catch((err) => {
        console.error(err)
        notifications.error("Can not push file to serve!", "Commit")
      })
    } 
  }

  // save content to localstorage Workspace
  saveContentToWorkspace = () => {
    const {
      configs,
      settings,
      updateLayout
    } = this.props

    const {
      author,
      comment,
      pushToServer,
    } = this.state

    const contentProject = window.localStorage.getItem(KEY_WORK_CONTENT)

    if (contentProject && configs && configs.currentProject) {
      const keyItem = configs.currentProject
      window.localStorage.setItem(keyItem, contentProject)

      const date = new Date()
      let nameProject = ""

      if (keyItem.includes(KEY_ITEM_FILE) === true) {
        nameProject = keyItem.replace(KEY_ITEM_FILE, "")
      }

      const contentCommit = {
        user: author,
        comment: comment.value,
        name: nameProject,
        key: configs.currentProject,
        date: `${date.getUTCFullYear()}/${date.getUTCMonth()}/${date.getUTCDate()} ${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}`,
        accessToken: settings.accessToken
      }

      if (pushToServer.value === true) {
        this.pushProjectToServer(contentCommit)
      } else {
        if (configs.listCommit === undefined || (configs.listCommit && configs.listCommit.length === 0)) {
          configs.listCommit = [contentCommit]
        } else {
          configs.listCommit.push(contentCommit)
        }

        window.localStorage.setItem(CONFIG_KEY, JSON.stringify(configs))

        notifications.success("save content at local successfully", "Commit")
      }

      this.setState({
        comment: {
          value: ""
        },
        pushToServer: {
          value: false
        }
      })

      updateLayout()
    } else {
      notifications.error("Can not save latest content", "Commit")
    }
  }

  renderHistoryLayout = () => {
    const {
      historiesServer
    } = this.props

    let layoutHtml = ""

    if (historiesServer && historiesServer.length > 0 ) {
      layoutHtml = historiesServer.map((item, index) => {
        return (
          <tr>
              <th scope="row">{index + 1}</th>
              <td>{item.name}</td>
              <td>{item.date}</td>
              <td>{item.user}</td>
              <td>{item.comment}</td>
          </tr>
        )
      })
    }

    return layoutHtml
  }

  render() {  
    const {
      comment,
      pushToServer,
      author,
    } = this.state

    return (
      <div className="right-panel">
        <div className="history-panel">
        <table className="table">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Name project</th>
              <th scope="col">Date</th>
              <th scope="col">Author</th>
              <th scope="col">Comment</th>
            </tr>
          </thead>
          <tbody>
            {this.renderHistoryLayout()}
          </tbody>
        </table>
        </div>
        <div className="commit-panel">
          <b>{author}</b>
          <textarea
            rows="3"
            id="comment"
            name="comment"
            onChange={this.handleInputChange}
            value={comment.value}
          >
          </textarea>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="pushToServer"
              name="pushToServer"
              checked={pushToServer.value}
              onChange={this.handleCheckboxChange}
            />
            <label className="form-check-label" htmlFor="pushToServer">
              Push to server
            </label>
          </div>
          <button
            className="btn btn-default btn-commit"
            onClick={this.saveContentToWorkspace}
          >
            Commit
          </button>
        </div>
    </div>
    )
  }
}

export default CommitComponent
