import React from "react"
import { NotificationManager as notifications } from "react-notifications"

const KEY_ITEM_FILE = "swagger-item-"
const CONFIG_KEY = "swagger-config"
const KEY_WORK_CONTENT = "swagger-editor-content"

class ProjectComponent extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      nameProject: {
        value: ""
      },
      checkoutNewProject:  {
        value: true
      },
      pushToServer:  {
        value: false
      },
      copyParent:  {
        value: false
      },
      valid: ""
    }
  }

  handleInputChange = (event) => {
    const stringValue = event.target.value
    const name = event.target.name

    const data = []

    const reWhiteSpace = new RegExp(/^\s+$/)

    if (reWhiteSpace.test(stringValue) === false) {
      data[name] = {
        value: stringValue.replace(" ", "")
      }

      data.valid = ""
    } else {
      data.valid = "Can not enter space in name project!"
    }

    this.setState(data)
  }

  handleCheckboxChange = (event) => {

    const stringValue = event.target.checked
    const name = event.target.name

    const data = []

    data[name] = {
      value: stringValue
    }

    this.setState(data)
  }

  handleSubmitNewProject = () => {
    const {
      specActions,
      configs,
      updateLayout,
      setTitleHeader,
    } = this.props

    const {
      nameProject,
      checkoutNewProject,
      copyParent,
    } = this.state

    const contentWorkspace = window.localStorage.getItem(KEY_WORK_CONTENT)

    if (window.localStorage && nameProject.value) {
      const keyForName = nameProject.value.toLowerCase().trim()
      const newNameProject = `${KEY_ITEM_FILE}${keyForName}`
      window.localStorage.setItem(newNameProject, "")

      // save name project
      let config = Object.assign(configs,
        {
          timeLastCreated: new Date().getTime()
        }
      )

      if (checkoutNewProject.value === true) {
        // save config
        config.currentProject = newNameProject

        setTitleHeader(nameProject.value)

        specActions.updateSpec("")
      }

      if (copyParent.value === true && contentWorkspace) {
        // save config
        if (checkoutNewProject.value === true) {
          specActions.updateSpec(contentWorkspace)
        }

        window.localStorage.setItem(newNameProject, contentWorkspace)
      }

      window.localStorage.setItem(CONFIG_KEY, JSON.stringify(config))

      this.setState({
        nameProject: {
          value: ""
        }
      })

      window.location.hash = ""

      updateLayout()

      notifications.success(`Create successful project: ${nameProject.value}`, "Create New Project")
    } else {
      notifications.error(`Please enter name project`, "Create New Project")
    }
  }

  render() {  
    const {
      nameProject,
      checkoutNewProject,
      pushToServer,
      copyParent,
      valid,
    } = this.state

    let validMessage = ""

    if (valid.length > 0) {
      validMessage = (
        <span style={{color: "red"}}>{valid}</span>
      )
    }

    return (
      <div className="right-panel">
        <div className="card">
          <div className="card-body">
            <div className="form-group">
              <label>Name project</label>
              <input
                type="text"
                className="form-control"
                name="nameProject"
                id="nameProject"
                value={nameProject.value}
                onChange={this.handleInputChange}
                pattern="[^\s]+"
                autoFocus
              />
              {validMessage}
              <span>{nameProject.value ? `file name: ${KEY_ITEM_FILE}${nameProject.value}.yml` : ""}</span>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                name="checkoutNewProject"
                id="checkoutNewProject"
                checked={checkoutNewProject.value}
                onChange={this.handleCheckboxChange}
              />
              <label className="form-check-label" htmlFor="checkoutNewProject">
                Checkout new project
              </label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                name="pushToServer"
                id="pushToServer"
                disabled
                checked={pushToServer.value}
                onChange={this.handleCheckboxChange}
              />
              <label className="form-check-label" htmlFor="pushToServer">
                Push project to server
              </label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                id="copyParent"
                name="copyParent"
                checked={copyParent.value}
                onChange={this.handleCheckboxChange}
              />
              <label className="form-check-label" htmlFor="copyParent">
                Copy content from parent project
              </label>
            </div>
          </div>
          <div className="card-footer">
            <button
              className="btn btn-info"
              style={{float: "right"}}
              onClick={this.handleSubmitNewProject}
            >
              Create new project
            </button>
          </div>
        </div>
    </div>
    )
  }
}

export default ProjectComponent
